<template>
    <div class="activity">
      <div class="container">
        <div class="activity-box" v-for="item in list">
          <div class="activity-title">{{item.post_title}}</div>
          <div class="activity-img">
            <!--<img src="../../images/net.jpg" alt=""/>-->
            <img :src="imgBaseUrl+item.more">
          </div>
        </div>
        <!--底部页码-->
        <div class="yema col-md-12" v-if="list.length !=0">
          <pagination ref="pagination" :total="total" :display="display" :current-page='current' @pagechange="pagechange"></pagination>
        </div>
      </div>
    </div>
</template>
<script>
  import {imgBaseUrl} from 'src/config/env';
  import pagination from 'src/components/pagination/pagination'  //分页
  export default {
    data() {
      return {
          list:'',
          total: 10,     // 记录总条数
          display: 2,   // 每页显示条数
          current: 1,   // 当前的页数
          problem:'',
          title: '活动专题 - 冀企管家',
          description: '互联网时代的企业管家',
          keywords: '冀企管家',
          imgBaseUrl
      }
    },
    metaInfo () {
      return {
        title: this.title,
        meta: [
          {                 // set meta
            name: 'description',
            content: this.description
          },
          {
            name: 'keywords',
            content:this.keywords
          }
        ]
      }
    },
    components: {
      'pagination': pagination
    },
    methods:{
      //   切换页码
      pagechange:function(currentPage){
        var that = this;
        that.current = currentPage;
        //   获取指定分类下的文章列表
        that.$api.get('/home/index/problem?page='+that.current+'', null, res => {
          if(res.code==1){
            that.problem = res.data.data;
          }
        })
      }
    },
    created(){
      let that=this;
      that.current = 1;
      that.$api.get('/home/index/pagelist',null, function (res) {
        if(res.code==1){
           that.list=res.data.data;
          that.total = res.data.total;
          that.display = res.data.per_page;
        }
      })
      this.$api.get('/home/index/seo/id/2',null, function (res) {
        if(res.code==1){
          that.title = res.data.seo_title;
          that.description = res.data.seo_describe;
          that.keywords = res.data.seo_keywords;
        }
      })

    }
  }
</script>
<style>
  .activity{
    background-color: #EEEEEE;
    min-height: 600px;
  }
  .activity-box{
    background-color: #fff;
    margin-top: 40px;
    margin-bottom: 40px;
  }
  .activity-title{
    font-size: 24px;
    color: #333;
    text-align: center;
    font-family: '黑体';
    line-height: 70px;
  }
  .activity-img img{
    width: 100%;
    max-height: 400px;
  }
  .yema{
    margin-bottom:40px;
  }
  /*响应式代码*/
  /*小屏情况下*/
  @media (max-width: 680px){
    .activity{
      min-height: 300px;
    }
    .activity-title{
      font-size: 20px;
      line-height: 50px;
    }
    .yema{
      margin-top: 40px;
    }
    .yema ul{
      float: left;
      padding-left: 0;
    }
  }
  @media (min-width: 768px) {
    .yema{
      margin-top: 60px;
    }
    .yema ul{
      float: right;
    }
  }
</style>



