<template>
    <div class="question">
      <div class="container">
        <div class="ask-title">常见问题</div>
        <div class="ask-box col-md-9" v-for="(item,index) in problem">
          <div class="ask"><span>{{index+1}}.</span><i></i><div>{{item.problem}}</div></div>
          <div class="answer"><i></i><div>{{item.answer}}</div></div>
        </div>
        <!--底部页码-->
        <div class="yema col-md-12" v-if="problem.length !=0">
          <pagination ref="pagination" :total="total" :display="display" :current-page='current' @pagechange="pagechange"></pagination>
        </div>
        <div class="ask-btn col-md-12">
          <router-link to="/question/ask" class='sq-btn'>我要提问</router-link>
        </div>
      </div>
    </div>
</template>
<script>
  import pagination from 'src/components/pagination/pagination'  //分页
  export default{
    name: 'question',
    data () {
    return {
      total: 10,     // 记录总条数
      display: 2,   // 每页显示条数
      current: 1,   // 当前的页数
      problem:'',
      title: '常见问题 - 冀企管家',
      description: '互联网时代的企业管家',
      keywords: '冀企管家',
    }
  },
  metaInfo () {
    return {
      title: this.title,
      meta: [
        {                 // set meta
          name: 'description',
          content: this.description
        },
        {
          name: 'keywords',
          content:this.keywords
        }
      ]
    }
  },
  components: {
    'pagination': pagination
  },
  mounted(){
    this.getproblem()
  },
  methods:{
    //   获取常见问题列表
    getproblem: function () {
      var that = this;
      that.current = 1;
      that.$api.get('/home/index/problem?page='+that.current+'', null, res => {
        if(res.code==1){
          that.problem = res.data.data;
          that.total = res.data.total;
          that.display = res.data.per_page;
        }
      })
    },
    //   切换页码
    pagechange:function(currentPage){
      var that = this;
      that.current = currentPage;
      //   获取指定分类下的文章列表
      that.$api.get('/home/index/problem?page='+that.current+'', null, res => {
        if(res.code==1){
          that.problem = res.data.data;
        }
      })
    }
  },
    created(){
      let that=this;
      this.$api.get('/home/index/seo/id/6',null, function (res) {
        if(res.code==1){
          that.title = res.data.seo_title;
          that.description = res.data.seo_describe;
          that.keywords = res.data.seo_keywords;
        }
      })
    }
  }
</script>
<style>
  .question{
    background-color: #EEEEEE;
    min-height: 520px;
  }
  .ask-title{
    font-size: 24px;
    color: #000;
    font-family: "黑体";
    text-align: center;
    border-bottom: 1px solid #CCCCCC;
    line-height: 80px;
    margin-bottom: 25px;
  }
  .ask-box{
    margin-left: 7%;
    margin-top: 25px;
    margin-bottom: 25px;
  }
  .ask{
    font-size: 14px;
    color: #666666;
    margin-bottom: 20px;
    padding-left: 96px;
    position: relative;
    min-height: 40px;
    line-height: 40px;
  }
  .ask span{
    font-size: 24px;
    color: #333;
    display: inline-block;
    position: absolute;
    left: 5px;
  }
  .ask i{
    position: absolute;
    background-image: url('../../images/wen.png');
    background-size: 36px 40px;
    width: 36px;
    height: 40px;
    left: 40px;
  }
  .ask div{
    padding-top: 9px;
    line-height: 24px;
  }
  .answer{
    font-size: 14px;
    color: #666666;
    padding-left: 96px;
    position: relative;
    min-height: 40px;
  }
  .answer i{
    position: absolute;
    background-image: url('../../images/da.png');
    background-size: 36px 40px;
    width: 36px;
    height: 40px;
    left: 40px;
  }
  .answer div{
    padding-top: 9px;
    line-height: 24px;
  }
  .ask-btn{
    text-align: center;
    margin-top: 40px;
    margin-bottom: 70px;
  }
  .sq-btn{
    width: 300px;
    height: 50px;
    line-height: 50px;
    display: inline-block;
    background-color: #0B357C;
    color: #ffffff;
    border-radius: 8px;
    font-size: 26px;
    margin-top: 20px;
    margin-bottom: 40px;
  }
  .sq-btn:hover{
    color: #ffffff;
  }
  /*小屏情况下*/
  @media (max-width: 992px) {
    .ask-box{
      margin-left: 0px;
    }
  }
  @media (max-width: 768px) {
    .ask-btn{
      text-align: center;
      margin-top: 30px;
      margin-bottom: 40px;
    }
    .sq-btn{
      width: 200px;
      height: 50px;
      border-radius: 5px;
      font-size: 22px;
    }
    .ask-box{
      padding-left: 0;
      padding-right: 0;
    }
    .yema{
      margin-top: 40px;
    }
    .yema ul{
      float: left;
      padding-left: 0;
    }
    .question{
      min-height: 320px;
    }
  }
  @media (min-width: 768px) {
    .yema ul{
      float: right;
    }
  }
</style>



