<template>
  <div class="shutter">
    <div class="shutter-img">
      <a v-for="item in swipers" href="javascript:void(0);"><img :src="imgBaseUrl+item.image"/></a>
    </div>
    <ul class="shutter-btn">
      <li class="prev"></li>
      <li class="next"></li>
    </ul>
  </div>
</template>

<style  scoped>
  li{
    list-style: none;
  }

  @media screen and (min-width: 2000px){
    .shutter{
      height: 1000px;
    }
  }
  @media screen and (min-width: 1500px) and (max-width: 2000px) {
    .shutter{
      height: 906px;
    }
  }
  @media screen and (min-width: 1200px) and (max-width: 1500px) {
    .shutter{
      height: 800px;
    }
  }
  @media screen and (min-width: 768px) and (max-width: 1200px) {
    .shutter{
      height: 600px;
    }
  }
  @media screen and (min-width: 479px) and (max-width: 768px) {
    .shutter{
      height: 400px;
    }
  }
  @media screen and (max-width: 479px) {
    .shutter{
      height: 230px;
    }
    .shutter-btn li.prev{
      display: none;
    }
    .shutter-btn li.next{
      display: none;
    }
  }
  .shutter {
    overflow: hidden;
    position: relative;
    margin: 0 auto;
  }
  .shutter-img {
    z-index: 1;
  }
  .shutter-img,
  .shutter-img a {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .shutter-img a {
    cursor: default;
  }
  .shutter-img a > img {
    width: 100%;
    height: 100%;
  }
  .shutter-img .created {
    overflow: hidden;
    position: absolute;
    z-index: 20;
  }
  .shutter-btn {}
  .shutter-btn li {
    position: absolute;
    z-index: 2;
    top: 50%;
    width: 49px;
    height: 49px;
    margin-top: -25px;
    cursor: pointer;
  }
  .shutter-btn li.prev {
    left: 20px;
    background: url(../../images/banner/shutter_prevBtn.png) no-repeat 0 -49px;
  }
  .shutter-btn li.next {
    right: 20px;
    background: url(../../images/banner/shutter_nextBtn.png) no-repeat 0 -49px;
  }
</style>

<script>
  import {imgBaseUrl} from 'src/config/env'
  window.jQuery = window.$ = require("jquery/dist/jquery.js");
  require("velocity-animate/velocity.js");
  require("velocity-animate/velocity.ui.js");
  export default {
    data(){
      return {
        swipers:'',
        curDisplay:1,
        shutterItem_len:0,
        imgBaseUrl
      }
    },
    props:{
      width:{
        type:String,
        default:'100%'
      },
      height:{
        type:String,
        default:''
      },
      isAutoPlay:{
        type:Boolean,
        default:true
      },
      playInterval:{
        type:Number,
        default:3000
      },
      showBtn:{
        type:Boolean,
        default:false
      },
      list:{
        type:Array,
        default:[

        ]
      }
    },
    methods:{
      play: function () {
        var _this = this;

        if (this.isAutoPlay) {
          clearInterval(this.playTime);

          this.playTime = setInterval(function () {
            _this.toggleMove('next');
          }, this.playInterval);
        }
      },
      moveSwitch: function (randomNum, command, index) {
        switch (randomNum) {
          case 0:
            this.gridWhole(index, 0);
            break;
          case 1:
            this.gridWhole(index, 1);
            break;
          case 2:
            this.gridWhole(index, 2);
            break;
          case 3:
            this.gridWhole(index, 3);
            break;
          case 4:
            this.gridTop(index, 0);
            break;
          case 5:
            this.gridTop(index, 1);
            break;
          case 6:
            this.gridTop(index, 2);
            break;
          case 7:
            this.gridLeft(index, 0);
            break;
          case 8:
            this.gridLeft(index, 1);
            break;
          case 9:
            this.gridLeft(index, 2);
            break;
          case 10:
            this.gridOpacity(index);
            break;
          case 11:
            this.gridAccordion(index);
            break;
          case 12:
            this.gridLittle(index);
            break;
          case 13:
            this.gridSwitch(index);
            break;
          default:
            this.gridTop(index, 0);
            break;
        }
      },
      toggleMove: function (command, index) {
//        console.log(command,index)
        if (!command) {
          if (this.curDisplay === index) {
            return;
          } else if (this.curDisplay === 0 && index === this.shutterItem_len - 1
            || index < this.curDisplay) {
            command = 'prev';
          } else {
            command = 'next';
          }
        }

        if (!index) {
          if (command === 'prev') {
            index = this.curDisplay - 1;

            if (this.curDisplay === 0) {
              index = this.shutterItem_len - 1;
            }
          } else {
            index = this.curDisplay + 1;

            if (this.curDisplay === this.shutterItem_len - 1) {
              index = 0;
            }
          }
        }

        this.$shutterDesc.animate({bottom: -36});

        var random = function (min, max) {
          return Math.floor(Math.random() * (max + 1) - min);
        };

        this.moveSwitch(random(0, 13), command, index);
      },
      initalShutter:function(){
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $nearlyElem = this.$shutterItem.not($curElem);

        $curElem.css('zIndex', 20);
        $nearlyElem.each(function (i) {
          $(this).css('zIndex', ++ i);
        });
      },
      init:function(){
        this.$shutter = $('.shutter');
        this.$shutterItem = this.$shutter.find('.shutter-img a');
        this.$prevBtn = this.$shutter.find('.shutter-btn .prev');
        this.$nextBtn = this.$shutter.find('.shutter-btn .next');
        this.$shutterNav = this.$shutter.find('.shutter-nav li');
        this.$shutterDesc = this.$shutter.find('.shutter-desc');

        this.shutterItem_len = this.$shutterItem.length;
        this.curDisplay = this.curDisplay > this.shutterItem_len - 1 ? this.curDisplay = this.shutterItem_len - 1 : this.curDisplay;
        this.b_stop = true;
        this.playTime = null;

        this.initalShutter();

        this.shutterW = this.$shutter.width();
        this.shutterH = this.$shutter.height();

        let _this = this;

        this.$prevBtn.bind('click', function () {
          if (_this.b_stop) {
            _this.b_stop = false;
            _this.toggleMove('prev');
          }
        });
        this.$nextBtn.bind('click', function () {
          if (_this.b_stop) {
            _this.b_stop = false;
            _this.toggleMove('next');
          }
        });

        this.play();
        this.$shutter.hover(function () {
          clearInterval(_this.playTime);
        }, function () {
          _this.play();
        });
      },
      // 图片切换方法
      recovery: function (target, cur, index, backup, interval) {
        var self = this;

        setTimeout(function () {
          target.css('zIndex', 20);
          cur.css('zIndex', self.curDisplay).html(backup);
          self.curDisplay = index;
          self.$shutterDesc.animate({bottom: 0}).find('p').text(self.shutterTitle);

          self.b_stop = true;
        }, interval);
      },
      gridWhole: function (index, showNum) {
        var self = this;
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $targetElem = this.$shutterItem.eq(index);
        var backup = $curElem.html();
        var $createElem = $('<div class="created"></div>');
        var movingVal = 0;

        $targetElem.css('zIndex', 19);
        $curElem.find('img').fadeOut();

        $createElem.html(backup).css({
          position: 'absolute',
          zIndex: 20,
          left: 0,
          top: 0,
          overflow: 'hidden',
          width: this.shutterW,
          height: this.shutterH
        });

        $curElem.append($createElem);

        if (showNum === 0) {
          movingVal = this.shutterW;

          $createElem.velocity({left: movingVal}, {duration: 1000});
        } else if (showNum === 1) {
          movingVal = -this.shutterW;

          $createElem.velocity({left: movingVal}, {duration: 1000});
        } else if (showNum === 2) {
          movingVal = this.shutterH;

          $createElem.velocity({top: movingVal}, {duration: 1000});
        } else if (showNum === 3) {
          movingVal = -this.shutterH;

          $createElem.velocity({top: movingVal}, {duration: 1000});
        }

        $createElem.find('img').css({
          display: 'block',
          width: this.shutterW,
          height: this.shutterH
        });

        this.recovery($targetElem, $curElem, index, backup, 1200);
      },

      gridTop: function (index, showNum) {
        var self = this;
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $targetElem = this.$shutterItem.eq(index);
        var backup = $curElem.html();
        var speed = 0;

        $targetElem.css('zIndex', 19);
        $curElem.find('img').fadeOut();
        for (var i = 0; i < 12; i ++) {
          var $createElem = $('<div class="created"></div>');

          $createElem.html(backup).css({
            position: 'absolute',
            zIndex: 20,
            left: this.shutterW / 12 * i,
            top: 0,
            overflow: 'hidden',
            width: this.shutterW / 12,
            height: this.shutterH
          });

          $curElem.append($createElem);

          $createElem.find('img').css({
            display: 'block',
            width: this.shutterW,
            height: this.shutterH,
            marginLeft: this.shutterW / -12 * i
          });
        }

        if (showNum === 0) {
          var movingVal = 0;

          $curElem.find('.created').each(function (i) {
            if (i % 2 === 0) {
              movingVal = self.shutterH;
            } else {
              movingVal = -self.shutterH;
            }

            $(this).velocity({top: movingVal}, {duration: 1000});
          });
        } else if (showNum === 1) {
          $curElem.find('.created').each(function (i) {
            speed = 80 * i;

            $(this).velocity({top: $(this).height()}, {duration: 120 + speed});
          });
        } else if (showNum === 2) {
          $curElem.find('.created').each(function (i) {
            speed = 80 * i;

            $(this).velocity({top: -$(this).height()}, {duration: 120 + speed});
          });
        }

        this.recovery($targetElem, $curElem, index, backup, 1000);
      },

      gridLeft: function (index, showNum) {
        var self = this;
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $targetElem = this.$shutterItem.eq(index);
        var backup = $curElem.html();
        var speed = 0;

        $targetElem.css('zIndex', 19);
        $curElem.find('img').fadeOut();

        for (var i = 0; i < 12; i ++) {
          var $createElem = $('<div class="created"></div>');

          $createElem.html(backup).css({
            position: 'absolute',
            zIndex: 20,
            left: 0,
            top: this.shutterH / 12 * i,
            overflow: 'hidden',
            width: this.shutterW,
            height: this.shutterH / 12
          });

          $curElem.append($createElem);

          $createElem.find('img').css({
            display: 'block',
            width: this.shutterW,
            height: this.shutterH,
            marginTop: this.shutterH / -12 * i
          });
        }

        if (showNum === 0) {
          var movingVal = 0;

          $curElem.find('.created').each(function (i) {
            if (i % 2 === 0) {
              movingVal = self.shutterW;
            } else {
              movingVal = -self.shutterW;
            }

            $(this).velocity({left: movingVal}, {duration: 1000});
          });
        } else if (showNum === 1) {
          $curElem.find('.created').each(function (i) {
            speed = 80 * i;

            $(this).velocity({left: $(this).width()}, {duration: 120 + speed});
          });
        } else if (showNum === 2) {
          $curElem.find('.created').each(function (i) {
            speed = 80 * i;

            $(this).velocity({left: -$(this).width()}, {duration: 120 + speed});
          });
        }

        this.recovery($targetElem, $curElem, index, backup, 1000);
      },

      gridOpacity: function (index) {
        var self = this;
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $targetElem = this.$shutterItem.eq(index);
        var backup = $curElem.html();
        var $createElem = $('<div class="created"></div>');

        $targetElem.css('zIndex', 19);
        $curElem.find('img').fadeOut();

        $createElem.html(backup).css({
          position: 'absolute',
          zIndex: 20,
          left: 0,
          top: 0,
          overflow: 'hidden',
          width: this.shutterW,
          height: this.shutterH,
          opacity: 1
        });

        $createElem.find('img').css({
          display: 'block',
          width: this.shutterW,
          height: this.shutterH
        });

        $curElem.append($createElem);

        $createElem.velocity({opacity: 0}, {duration: 1000});

        this.recovery($targetElem, $curElem, index, backup, 1000);
      },

      gridAccordion: function (index) {
        var self = this;
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $targetElem = this.$shutterItem.eq(index);
        var backup = $curElem.html();
        var iNow = 0;
        var speed = 0;

        $targetElem.css('zIndex', 19);
        $curElem.find('img').fadeOut();

        for (var i = 0; i < 12; i ++) {
          var $createElem = $('<div class="created"></div>');

          $createElem.html(backup).css({
            position: 'absolute',
            zIndex: 20,
            left: this.shutterW / 12 * i,
            top: 0,
            overflow: 'hidden',
            width: this.shutterW / 12,
            height: this.shutterH,
            opacity: 1
          });

          $curElem.append($createElem);

          $createElem.find('img').css({
            display: 'block',
            width: this.shutterW,
            height: this.shutterH,
            marginLeft: this.shutterW / -12 * i
          });
        }

        $curElem.find('.created').each(function (i) {
          speed = i * 80;

          $(this).velocity({opacity: 0}, {duration: 320 + speed});
        });

        this.recovery($targetElem, $curElem, index, backup, 1200);
      },

      gridLittle: function (index) {
        var self = this;
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $targetElem = this.$shutterItem.eq(index);
        var backup = $curElem.html();
        var coordinate = null;

        $targetElem.css('zIndex', 19);
        $curElem.find('img').fadeOut();

        for (var i = 0; i < 24; i ++) {
          var $createElem = $('<div class="created"></div>');

          $createElem.html(backup).css({
            width: this.shutterW / 6,
            height: this.shutterH / 4,
            left: (this.shutterW / 6) * (i % 6),
            top: (this.shutterH / 4) * Math.floor(i / 6)
          });

          $curElem.append($createElem);

          $createElem.find('img').css({
            display: 'block',
            width: this.shutterW,
            height: this.shutterH,
            marginLeft: -(this.shutterW / 6) * (i % 6),
            marginTop: -(this.shutterH / 4) * Math.floor(i / 6)
          });
        }

        coordinate = this.getXY($curElem.find('.created'), 4, 6);

        this.tab(coordinate, 0, 0, function () {
          var left = parseInt(this.style.left);
          var top = parseInt(this.style.top);

          $(this).velocity({
            left: left + 100,
            top: top + 100,
            opacity: 0
          });
        }, 100, +1, +1);

        this.recovery($targetElem, $curElem, index, backup, 1200);
      },

      gridSwitch: function (index) {
        var self = this;
        var $curElem = this.$shutterItem.eq(this.curDisplay);
        var $targetElem = this.$shutterItem.eq(index);
        var backup = $curElem.html();

        $targetElem.css('zIndex', 19);
        $curElem.find('img').fadeOut();

        for (var i = 0; i < 20; i ++) {
          var $createElem = $('<div class="created"></div>');

          $createElem.html(backup).css({
            width: this.shutterW / 5,
            height: this.shutterH / 4,
            left: (this.shutterW / 5) * (i % 5),
            top: (this.shutterH / 4) * Math.floor(i / 5)
          });

          $curElem.append($createElem);

          $createElem.find('img').css({
            display: 'block',
            width: this.shutterW,
            height: this.shutterH,
            marginLeft: -(this.shutterW / 5) * (i % 5),
            marginTop: -(this.shutterH / 4) * Math.floor(i / 5)
          });
        }

        $curElem.find('.created').each(function (i) {
          if (i % 2 === 0) {
            $(this).find('img').velocity({
              marginLeft: $(this).width()
            }, {duration: 500});
          }
        });

        setTimeout(function () {
          $curElem.find('.created').each(function (i) {
            if (i % 1 === 0) {
              $(this).find('img').velocity({
                marginLeft: $(this).width()
              }, {duration: 500});
            }
          });
        }, 600);

        this.recovery($targetElem, $curElem, index, backup, 1200);
      },
      tab:function(arr, x, y, fn, delay, speedX, speedY) {
        let _this = this;
        if (!arr[y] || !arr[y][x]) {
          return;
        }

        if (fn) {
          fn.call(arr[y][x]);

          clearTimeout(arr[y][x].timer);
          arr[y][x].timer = setTimeout(function () {
            _this.tab(arr, x, y + speedY, fn, delay, speedX, speedY);
            _this.tab(arr, x + speedX, y, fn, delay, speedX, speedY);
          }, delay);
        }
      },
      getXY:function(objs, rows, cols) {
        var arr1 = [];

        for (var i = 0; i < rows; i ++) {
          var arr2 = [];

          for (var j = 0; j < cols; j ++) {
            objs[i * cols + j].xIndex = j;
            objs[i * cols + j].yIndex = i;

            arr2.push(objs[i * cols + j]);
          }

          arr1.push(arr2);
        }
        return arr1;
      }
    },
    created:function(){
      /**
       *轮播图接口调用
       */
      var that = this;
      this.$api.get('/home/slides/14',null, function (res) {
        if(res.code==1){
          that.swipers = res.data
          that.$nextTick(function () {
            that.init()
          })
        }
      })
    }
  }
</script>





