import Vue from 'vue'
import Router from 'vue-router'
import index from 'src/pages/index/index'
import List from 'src/pages/list/list'
import Aboutus from 'src/pages/aboutus/aboutus'
import Apply from 'src/pages/apply/apply'
import Cooperation from 'src/pages/apply/cooperation'
import Investment from 'src/pages/apply/investment'
import Question from 'src/pages/question/question'
import Ask from 'src/pages/ask/ask'
import Activity from 'src/pages/activity/activity'
import Success from 'src/pages/success/success'
import Articles from 'src/pages/list/children/articles'


Vue.use(Router)

export default new Router({
  mode: 'history',//可以是去掉#号
  routes: [
    {
      path: '',
      redirect: '/home'
    },
    {
      path: '/home',
      component: index,
    },
    {
      path: '/articles/:id',
      component: Articles,
    },
    {
      path: '/list/:id',
      component: List,
    },
    {
      path: '/aboutus',
      component: Aboutus,
    },
    {
      path: '/apply',
      component: Apply,
    },
    {
      path: '/cooperation',
      component: Cooperation,
    },
    {
      path: '/investment',
      component: Investment,
    },
    {
      path: '/question/ask',
      component: Ask,
    },
    {
      path: '/success',
      component: Success,
    },
    {
      path: '/question',
      component: Question,
    },
    {
      path: '/activity',
      component: Activity,
    },
  ]
})



