<template>
    <div class="web-footer">
      <!--固定电话 qq-->
      <div class="tel-qq">
        <!--qq-->
        <a class="qq_iframe" :href="qqhref">
          <div class="box0 box-qq">
            <i class="icon-qq"></i>
            <span class="icon-qqzi">企业QQ</span>
            <input type="hidden" id="qq" :value="publicmsg.site_qq">
          </div>
        </a>
        <!--电话-->
        <div class="box0 box-tel">
          <i class="icon-tel"></i>
          <span class="icon-telzi">电话联系</span>
          <div class="bigtel">{{publicmsg.site_tel}}</div>
        </div>
        <!--留言-->
        <div class="box0 box-msg" data-toggle="modal" data-target="#myModal">
          <i class="icon-msg"></i>
          <span class="icon-telzi">给我留言</span>
        </div>
        <!--留言-->
        <div class="box0 box-msg">
          <i class="icon-msg1" @click="bo"></i>
          <span class="icon-telzi">播放音乐</span>
        </div>
        <!--微信-->
        <div class="box0 box-ma">
          <i class="icon-ma"></i>
          <span class="icon-mazi">关注我们</span>
          <div class="bigma">
            <img :src="imgBaseUrl+erweima" width="160" height="160" alt=""/>
          </div>
        </div>
      </div>
      <!--留言模态窗-->
      <div class="modal fade liuyan" id="myModal" tabindex="-1" role="dialog" aria-labelledby="gridSystemModalLabel">
      <div class="modal-dialog" role="document">
      <div class="modal-content">
      <form id="me" action="/portal/message/index" method="post">
      <div class="modal-header">
      <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
      <h4 class="modal-title" id="gridSystemModalLabel">给我留言：</h4>
      </div>
      <div class="modal-body">
      <div class="form-group">
      <label class="control-label2">留言内容</label>
      <textarea id="mess" name="message" v-model="message" class="form-control form-control2 liuyan_text"></textarea>
      </div>
      <div class="form-group">
      <label class="control-label2">联系人</label>
      <input id="us" name="user" class="form-control form-control2" v-model="tel" placeholder="请输入联系人" >
      </div>
      <div class="form-group">
      <label class="control-label2">联系电话</label>
      <input id="tel" name="tel" class="form-control form-control2" v-model="user" placeholder="请输入联系人电话" >
      </div>
      <div class="form-group yzm">
      <label class="control-label2">验证码</label>
      <input id="cap"  name="captcha" class="form-control form-control2" v-model="code" placeholder="请输入验证码">
        <img :src="baseimg" alt="" @click='tabimg'/>
      </div>
      </div>
      <div class="modal-footer">
      <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
      <button id="fs" type="button" @click='btn' class="btn btn-primary">发送</button>
      </div>
      </form>
      </div>
      </div>
      </div>

      <div class="container">
        <img src="../images/tops.png" id="btn_top" alt=""/>
        <audio src="../../static/music/a.mp3" id="music1" controls="controls">
          Your browser does not support the audio element.
        </audio>
        <!-- 网站地图 -->
        <div class="map-footer row">
          <div class="map col-md-6 col-sm-12 hidden-xs">
            <dl class="col-md-3 col-sm-3" v-for="i in footlist">
              <dt>{{i.name}}</dt>
              <dd v-for="k in i.subnavigation"><router-link :to="k.href">{{k.name}}</router-link></dd>
            </dl>
          </div>
          <div class="map_tel col-md-3 col-sm-12">
            <p>联系电话：{{publicmsg.site_tel}}</p>
            <p>{{publicmsg.site_hours}}</p>
          </div>
          <div class="map_weixin col-md-3 col-sm-12 hidden-xs">
            <div class="text-center">
              <img :src="imgBaseUrl+erweima" alt=""/>
            </div>
          </div>
          <div class="map_link col-sm-12 col-xs-12">
            <span>友情链接：</span>
            <a :href="i.url" v-for="i in friendlink" target="_blank">{{i.name}}</a>
          </div>
        </div>
      </div>
      <!-- 底部 -->
      <div class="container">
        <div class="row " id="patent-footer">
          <div>
            <p>{{publicmsg.site_icp}}</p>
            <router-link class="on" :to="item.href" v-for="item in list" :key="item.id"> {{item.name}} </router-link>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
  /*回到顶部*/
  $(window).scroll(function () {
    if ($(window).scrollTop() >= 70) {
      $('#btn_top').fadeIn();
    }
    else {
      $('#btn_top').fadeOut();
    }
    var oTop = document.getElementById("btn_top");
    oTop.onclick = function(){
      document.documentElement.scrollTop = document.body.scrollTop =0;
    }
  });

  import {imgBaseUrl} from 'src/config/env'   //图片地址

  export default{
    name: 'web-footer',
    data () {
    return {
      message:'',
      tel:'',
      user:'',
      code:'',
      footlist:'',
      publicmsg:'',
      friendlink:'',
      erweima:'',
      qqhref:'',
      baseimg:'',
      list:'',
      imgBaseUrl
    }
  },
  methods:{
    bo:function () {
      var audio = document.getElementById('music1');
      if(audio!==null){
        //检测播放是否已暂停.audio.paused 在播放器播放时返回false.
        if(audio.paused)                     {
          audio.play();//audio.play();// 这个就是播放
        }else{
          audio.pause();// 这个就是暂停
        }
      }
      },
    btn:function(){
      var that = this;
      if ($.trim(that.message)=="") {
        alert("请输入留言");
        return false;
      }
      if ($.trim(that.tel)=="") {
        alert("请输入联系电话");
        return false;
      }
      if ($.trim(that.user)=="") {
        alert("请输入联系人名称");
        return false;
      }
      if ($.trim(that.code)=="") {
        alert("请输入验证码");
        return false;
      }
      const formData = new FormData()
      formData.append('message', that.message)
      formData.append('tel', that.tel)
      formData.append('user', that.user)
      formData.append('code', that.code)
      //   获取提交留言
      that.$api.post('/home/index/question', formData , res => {
      if(res.code==0){
        alert(res.msg)
      }
      if(res.code==1){
      }
    }, rrr  =>{
      console.log(rrr+'错误')
    })
  },
    //  获取验证码
    getcaptcha:function(){
      var that = this;
      that.$api.get('/home/index/captcha', null, res => {
        if(res.code==1){
        that.baseimg = res.data;
        }
      })
    },
    tabimg:function(){
      this.getcaptcha()
    }
  },
  mounted(){
    var that = this;
    that.getcaptcha()
    //   获取底部导航
    that.$api.get('/home/index/footer', null, res => {
      if(res.code==1){
        that.footlist = res.data;
      }
    })
    //   获取公共信息
    that.$api.get('/home/index/info', null, res => {
      if(res.code==1){
        that.publicmsg = res.data;
        that.erweima = res.data.site_thumbnail;
        //在线qq
        var qq_list = res.data.site_qq.split(",");
        //随机qq
        var qq_i = Math.floor(Math.random()*qq_list.length);
        var qqhref = "tencent://message/?uin="+qq_list[qq_i]+"&Site=&menu=yes";
        that.qqhref = qqhref;
      }
    })
    //   获取友情链接
    that.$api.get('/home/index/link', null, res => {
      if(res.code==1){
        that.friendlink = res.data;
      }
    })
    //   获取法律声明
    that.$api.get('/home/index/copyright',null, function (res) {
      if(res.code==1){
        that.list=res.data;
      }
    })
  }
  }
</script>
<style>
  /*返回顶部*/
  #btn_top {
    position: fixed;
    width: 50px;
    height: 50px;
    bottom: 10px;
    right: 10px;
    display: none;
    z-index: 9999;
    background-color: rgba(255,255,255,0.3);
    border: none;
  }
  /*##########页脚##########*/
  /*网站地图*/
  #music1{
    display: none;
  }
  .web-footer{
    padding-top: 40px;
    width:100%;
    background-color: #3A3A3A;
    box-shadow: 0 0 5px #000000;
  }
  .map-footer{
    border-bottom: 1px solid white;
    padding-bottom: 6px;
  }
  .map-footer dt{
    text-align: left;
    padding:3px;
    font-size: 16px;
    color:#e6e6e6;
  }
  .map-footer dd{
    padding:3px;
    font-size: 14px;
    color: #ccc;
    text-align: left;
  }
  .map-footer p{
    color:#fff;
    font-size:16px;
    text-align:center;
  }
  .map_tel{
    margin-top: 30px;
    margin-bottom: 20px;
  }
  .map_weixin img{
    width: 150px;
    height: 150px;
    margin-bottom: 15px;
  }
  .map_link{
    text-align: left;
  }
  .map_link span{
    color: #ffffff;
    text-align: left;
  }
  .map_link a{
    margin-right: 10px;
  }
  .map-footer a{
    color:#A6A6A6;
    font-size:13px;
    text-decoration:none;
  }
  .map-footer a:hover{
    color:#fff;
    text-decoration:none;
  }

  /*底部版权*/
  #patent-footer{
    text-align:left;
  }
  #patent-footer div{
    margin-top:15px;
    color:#8d8d8d;
    font-size:13px;
  }
  #patent-footer p{
    display: inline-block;
  }
  #patent-footer a{
    color: #666;
  }

  /*固定电话和qq*/
  .tel-qq .qq_iframe{
    text-decoration:none;
  }
  .tel-qq{
    width: 71px;
    position: fixed;
    right: 0px;
    top: 50%;
    margin-top: -146px;
    z-index: 122;
  }
  .box0{
    width: 73px;
    height: 73px;
    border: 1px solid #032e84;
    text-align: center;
  }
  .box-qq{
    height: 74px;
    padding-top:8px;
    background: #032e84;
  }
  .icon-qq{
    width: 40px;
    height: 40px;
    display: block;
    margin-right: auto;
    margin-left: auto;
    background-image: url("../images/qq1.png");
    background-size: 40px;
  }
  .box0 .icon-qqzi{
    line-height: 27px;
    color: white;
  }
  .box-tel{
    border-top: 0;
    border-bottom: 0;
    padding-top:8px;
    background: white;
    cursor: pointer;
  }
  .icon-tel{
    width: 40px;
    height: 40px;
    display: block;
    margin-right: auto;
    margin-left: auto;
    background-image: url("../images/phone1.png");
  }
  .bigtel{
    width: 150px;
    height: 45px;
    position: absolute;
    background-color: #fff;
    right: -100px;
    top: 86px;
    padding: 10px;
    display: block;
    box-sizing: border-box;
    border: 1px solid #ccc;
    font-size: 16px;
    color: orangered;
    font-weight:600;
    overflow: hidden;
    transition: all 0.5s;
    z-index: -1;
  }
  .box-tel:hover .bigtel{
    right: 71px;
  }
  .box0 .icon-telzi{
    line-height: 29px;
    color: #032e84;
  }
  .box-msg{
    border-bottom: 0;
    padding-top:8px;
    background: white;
    cursor: pointer;
  }
  .icon-msg{
    width: 40px;
    height: 40px;
    display: block;
    margin-right: auto;
    margin-left: auto;
    background-image: url("../images/liuyan.png");
  }
  .icon-msg1{
    width: 40px;
    height: 40px;
    display: block;
    margin-right: auto;
    margin-left: auto;
    background-image: url("../images/yinyue.png");
    background-size: 40px;
  }
  .box-ma{
    padding-top:7px;
    background: white;
    position: relative;
    cursor: pointer;
  }
  .box-ma:hover .bigma{
    display: block;
  }
  .icon-ma{
    width: 40px;
    height: 39px;
    display: block;
    margin-right: auto;
    margin-left: auto;
    background-image: url("../images/erweima.png");
  }
  .box0 .icon-mazi{
    line-height: 27px;
    color: #032e84;
  }
  .bigma{
    width: 180px;
    position: absolute;
    background-color: #fff;
    right: 72px;
    top: 0;
    padding: 12px;
    display: none;
    box-sizing: border-box;
    border: 1px solid #ccc;
  }

  .yzm img{
    width: 200px;
    height: 50px;
  }
  .yzm input{
    width: 200px;
    height: 50px;
    margin-right: 20px;
    display: inline-block;
  }
  /*留言*/
  .liuyan .modal-dialog{
    width: 800px;
  }
  .liuyan .modal-title{
    font-size: 20px;
  }
  .liuyan .modal-body{
    padding-top: 40px;
    padding-left: 65px;
    padding-right: 65px;
  }
  .liuyan .control-label2{
    float: left;
    width: 20%;
    line-height: 40px;
    font-size: 24px;
    font-weight: 400;
  }
  .liuyan .liuyan_text{
    min-height: 200px;
  }
  /*响应式代码*/
  /*小屏情况下*/
  @media (max-width: 768px){
    .tel-qq{
      display: none;
    }
    .map{
      display: none;
    }
    .map-footer .map_tel{
      margin-top: 0;
    }
    #patent-footer div{
      text-align: center;
      padding-bottom: 10px;
    }
    #patent-footer p{
      display: block;
    }
  }

</style>



