<template>
    <div class="articles container">
      <div class="articles-title">{{articles.post_title}}</div>
      <div class="articles-msg">
        <span v-if="articles.post_source !=''">来源：{{articles.post_source}}</span>
        <span>发布日期：{{articles.published_time}}</span>
        <span>浏览：{{articles.post_hits}}</span>
      </div>
      <div class="articles-content" v-html="articles.post_content">{{articles.post_content}}</div>
      <div class="post_source" v-if="articles.post_source !=''">注：本文转载自 <a :href="articles.post_source_url" target="_blank">{{articles.post_source}}</a> ，转载目的在于传递更多信息，并不代表本网赞同其观点和对其真实性负责。如有侵权行为，请联系我们，我们会及时删除。</div>
      <div class="articles-next">
        <div><span>上一篇</span>：<router-link :to="'/articles/' + last.id">{{last.title}}</router-link></div>
        <div><span>下一篇</span>：<router-link :to="'/articles/' + next.id">{{next.title}}</router-link></div>
      </div>
    </div>
</template>
<script>
  export default{
    name: 'articles',
    data () {
    return {
      articles:'',
      title: '互联网时代的企业管家 - 冀企管家',
      description: '互联网时代的企业管家',
      keywords: '冀企管家',
      last:'',
      next:''
    }
  },
  mounted(){
    this.getarticles()
  },
    metaInfo () {
      return {
        title: this.title,
        meta: [
          {
            name: 'description',
            content: this.description
          },
          {
            name: 'keywords',
            content:this.keywords
          }
        ]
      }
    },
  watch: {
    // 如果路由有变化，会再次执行该方法
    "$route": "getarticles"
  },
  methods:{
    //   获取文章数据
    getarticles: function () {
      var that = this;
      var id=that.$route.params.id;
      //  获取文章信息
      that.$api.get('/portal/articles/'+id+'', null, res => {
        if(res.code==1){

          that.articles = res.data;
          that.last = res.data.last;
          that.next = res.data.next;
          that.title = res.data.post_title;
          that.description = res.data.post_excerpt;
          that.keywords = res.data.post_keywords;
        }
      })
    }
  }
  }
</script>
<style>
  .articles-title{
    text-align: center;
    font-size: 24px;
    margin-top: 40px;
    margin-bottom: 20px;
    font-weight: bold;
    color: #333333;
  }
  .articles-msg{
    text-align: center;
    margin-bottom: 10px;
  }
  .articles-msg span{
    color: #666666;
    font-size: 14px;
    padding-left: 25px;
    padding-right: 25px;
  }

  .post_source{
    margin-top: 40px;
    margin-bottom: 25px;
  }
  .post_source span{
    color:red;
  }
  .articles-next{
    border-top: 1px solid #cfcfcf;
    padding-top: 22px;
    padding-bottom: 35px;
    font-size: 16px;
    color: #666;
  }
  .articles-next div{
    padding-left: 70%;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  /*响应式代码*/
  /*小屏情况下*/
  @media (max-width: 768px){
    .articles .articles-content img{
      width: 100% !important;
      height: 200px !important;
    }
    .articles-msg span{
      width: 100%;
      padding-left: 0px;
      padding-right: 0px;
      text-align: center;
      display: inline-block;
    }
    .articles-next div{
      padding-left: 0;
    }
  }
</style>



