<script>
  import banner from '../../components/banner/banner.vue';
  import {imgBaseUrl} from 'src/config/env';
  export default {
    data(){
      return {
        apw:'1',
        advantage:[],//冀企优势
        ids:[],
        depthRealize:[],//深入了解
        joinWe:[],//加入我们
        title: '互联网时代的企业管家 - 冀企管家',
        description: '互联网时代的企业管家',
        keywords: '冀企管家',
        imgBaseUrl
      }
    },
    metaInfo () {
      return {
        title: this.title,
        meta: [
          {                 // set meta
            name: 'description',
            content: this.description
          },
          {
            name: 'keywords',
            content:this.keywords
          }
        ]
      }
    },
    components:{
      v_banner:banner
    },
    methods:{
      more: function () {
        this.$router.push({path:'/list/32'})
      }
    },
    created:function(){
      var that = this;
      /**
       * 冀企的优势的接口   thumbnail
       */
      this.$api.get('/home/index/advantage',null, function (res) {
        if(res.code===1){
          that.advantage=res.data;
        }
      })
      /**
       * 深入了解的接口调用
       */
      this.$api.get('/portal/lists/getCategoryPostLists/category_id/32',null, function (res) {
        if(res.code===1){
          that.depthRealize=res.data.list.data;
        }
      })
      /**
       *  加入我们
       */
      this.$api.get('/home/index/corner',null, function (res) {
        that.joinWe=res.data;
      })

      /**
       * 标题
       */
      this.$api.get('/home/index/info',null, function (res) {
        if(res.code==1){
          that.title = res.data.site_seo_title;
          that.description = res.data.site_seo_description;
          that.keywords = res.data.site_seo_keywords;
        }
      })
    }
  }
</script>

