<template>
  <div class="apply">
    <div class="container">
      <div class="sq_title">我要提问</div>
      <div class="sq_box">
        <form id="sq" @submit.prevent="submit">
          <div class="form-group">
            <label class="control-label" for="cname"><i>*</i>姓名</label>
            <input id="cname" type="text" style="width: 60%" class="form-control" v-model='datamsg.name' placeholder="请输入姓名">
          </div>
          <div class="form-group">
            <label class="control-label" for="contact"><i>*</i>联系方式</label>
            <input id="contact" type="text" style="width: 60%" class="form-control" v-model="datamsg.tel" placeholder="请输入联系方式">
          </div>
          <div class="form-group">
            <label class="control-label" for="gangwei"><i>*</i>问题描述</label>
            <input id="gangwei" type="text" style="width: 60%" class="form-control" v-model="datamsg.problem" placeholder="请输入问题描述">
          </div>
          <div class="form-group yzm">
            <label class="control-label" for="captch"><i>*</i>验证码</label>
            <input  id="captch" type="text" class="form-control" v-model="datamsg.code" placeholder="请输入验证码">
            <img :src="baseimg" alt="" @click='tabimg'/>
          </div>
          <div class="form-group text-center">
            <button class="sq-btn" type="button" @click='btn'>提交</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data() {
    return {
      baseimg:'',
      datamsg:{
        name:'',
        tel:'',
        problem:'',
        code:''
      },
      title: '提问 - 冀企管家',
      description: '互联网时代的企业管家',
      keywords: '冀企管家',
    }
  },
  metaInfo () {
    return {
      title: this.title,
      meta: [
        {                 // set meta
          name: 'description',
          content: this.description
        },
        {
          name: 'keywords',
          content:this.keywords
        }
      ]
    }
  },
  methods:{
    btn:function(){
      var that = this;
      if ($.trim(that.datamsg.name)=="") {
        alert("请输入姓名");
        return false;
      }
      if ($.trim(that.datamsg.tel)=="") {
        alert("请输入联系方式");
        return false;
      }
      if ($.trim(that.datamsg.problem)=="") {
        alert("请输入问题描述");
        return false;
      }
      if ($.trim(that.datamsg.code)=="") {
        alert("请输入验证码");
        return false;
      }
      //   获取提交问题
      const formData = new FormData()
      formData.append('name', that.datamsg.name)
      formData.append('tel', that.datamsg.tel)
      formData.append('problem', that.datamsg.problem)
      formData.append('code', that.datamsg.code)
      formData.append('code_id', that.datamsg.code_id)
      that.$api.post('/home/index/question', formData , res => {
      if(res.code==0){
        alert(res.msg)
      }
        if(res.code==1){
          this.$router.push({path:'/success'})
        }
      }, rrr  =>{
        console.log(rrr+'错误')
      })
    },
  //  获取验证码
  getcaptcha:function(){
    var that = this;
    that.$api.get('/home/index/captcha', null, res => {
      if(res.code==1){

      that.baseimg = res.data;

    }
  })
  },
  tabimg:function(){
    this.getcaptcha()
  }
  },
  created(){
    let that=this;
    this.getcaptcha();
    this.$api.get('/home/index/seo/id/7',null, function (res) {

      if(res.code==1){
        that.title = res.data.seo_title;
        that.description = res.data.seo_describe;
        that.keywords = res.data.seo_keywords;
      }
    })
  }
  }
</script>
<style>
  /*表单样式*/
  .apply{
    background-color: #EEEEEE;
  }
  .sq_title{
    font-size: 24px;
    color: #000;
    font-family: "黑体";
    text-align: center;
    border-bottom: 1px solid #CCCCCC;
    line-height: 80px;
  }
  .sq_box{
    padding-top: 50px;
  }
  .form-group{
    overflow: hidden;
    margin-bottom: 40px;
  }
  .control-label{
    float: left;
    width: 150px;
    line-height: 40px;
    margin-right: 40px;
    font-size: 24px;
    font-weight: 400;
  }
  .control-label i{
    color: red;
    display: inline-block;
    width: 20px;
    line-height: 40px;
    text-align: left;
  }
  .form-control{
    height: 40px;
    float: left;
    width: 70%;
    font-size: 18px;
  }
  .form-group .form-control option{
    height: 37px;
  }
  .sq-btn{
    width: 300px;
    height: 50px;
    background-color: #0B357C;
    color: #ffffff;
    border-radius: 8px;
    font-size: 26px;
    margin-top: 20px;
    border: none;
    margin-bottom: 40px;
  }
  .yzm img{
    width: 200px;
    height: 50px;
  }
  .yzm input{
    width: 200px;
    height: 50px;
    margin-right: 20px;
  }
  /*小屏情况下*/
  @media (max-width: 768px) {
    .sq-btn{
      width: 200px;
      height: 50px;
      border-radius: 5px;
      font-size: 22px;
    }
  }
</style>



