<template>
    <div class="list">
      <div class="banner">
        <img class="banner-big" :src="imgBaseUrl+banner.thumbnail" alt=""/>
        <img class="banner-small" :src="imgBaseUrl+banner.thumbnail_small" alt=""/>
      </div>
      <div class="main">
        <div class="container p0">
          <!--左部文章-->
          <div class="main-left col-md-8 p0">
            <!--二级导航-->
            <div class="menu">
              <div v-for="(item,$index) in subcategory"  @click='selectStyle (item, $index,item.id)' :class="{'active':item.active,'':!item.active}">{{item.name}}</div>
              <div class="active" v-if="subcategory.length ==0">{{name}}</div>
            </div>
            <!--文章列表-->
            <div class="news">
              <div class="list_group clearfix" v-for="item in newspage">
                <div class="list_img" v-if="item.thumbnail!=''">
                  <router-link :to="'/articles/' + item.id"><img :src="imgBaseUrl+item.thumbnail" alt="" /></router-link>
                </div>
                <div class="list_mes">
                  <div class="heigh">
                    <a href="#">
                      <div class="mes_title"><router-link :to="'/articles/' + item.id">{{item.post_title}}</router-link></div>
                    </a>
                    <div class="mes_content1">{{item.post_excerpt}}</div>
                  </div>
                  <div class="mes_content2 clearfix">
                    <div class="mes_keyword">
                      <span v-for="k in item.post_keywords" v-if="item.post_keywords[0]!=''">{{k}}</span>
                    </div>
                    <div class="mes_time text-right">{{item.date}}-{{item.day}}</div>
                  </div>
                </div>
              </div>
            </div>
            <!--底部页码-->
            <div class="yema col-md-12 clearfix">
              <pagination ref="pagination" :total="total" :display="display" :current-page='current' @pagechange="pagechange"></pagination>
            </div>
          </div>
          <!--右部热门文章-->
          <div class="main-right col-md-4">
            <div class="article">
              <div class="article-title">
                <span>推荐</span>
              </div>
              <ul class="article-content">
                <li v-for="i in recommend"><router-link :to="'/articles/' + i.id">{{i.post_title}}</router-link></li>
              </ul>
            </div>
            <div class="article">
              <div class="article-title">
                <span>热门文章</span>
              </div>
              <ul class="article-content">
                <li v-for="i in hotpage"><router-link :to="'/articles/' + i.id">{{i.post_title}}</router-link></li>
              </ul>
            </div>
          </div>

        </div>
      </div>
    </div>
</template>
<script>
  import Vue from 'vue'
  import {imgBaseUrl} from 'src/config/env'   //图片地址
  import pagination from 'src/components/pagination/pagination'  //分页
  export default {
    name: 'list',
    data () {
      return {
        banner:'',        //banner图片
        name:'',          //默认导航名字
        subcategory:'',   //子分类导航
        newspage:'',      //文章列表
        recommend:'',     //推荐文章
        hotpage:'',       //热门文章
        idx:'',           //二级分类id
        total: 10,     // 记录总条数
        display: 2,   // 每页显示条数
        current: 1,   // 当前的页数
        title: '互联网时代的企业管家 - 冀企管家',
        description: '互联网时代的企业管家',
        keywords: '冀企管家',
        imgBaseUrl
      }
    },
    metaInfo () {
      return {
        title: this.title,
        meta: [
          {
            name: 'description',
            content: this.description
          },
          {
            name: 'keywords',
            content:this.keywords
          }
        ]
      }
    },
    components: {
      'pagination': pagination
    },
    watch: {
      // 如果路由有变化，会再次执行该方法
      "$route": "getmsg"
    },
    methods:{
      //   获取数据
      getmsg: function () {
        var that = this;
        var id=this.$route.params.id;  //主导航id
        //   获取指定分类的子分类
        that.$api.get('/portal/categories/subcategory/id/'+id+'', null, res => {
          if(res.code==1){
            that.banner = res.data.more;
            that.name = res.data.name;
            that.subcategory = res.data.son_list;
            that.title = res.data.seo_title;
            that.description = res.data.seo_description;
            that.keywords = res.data.seo_keywords;
            if(res.data.son_list.length ==0){
              that.idx = id;
            }else{
              var idx = res.data.son_list[0].id;  //默认二级导航id
              that.idx = idx;
              Vue.set(that.subcategory[0],'active',true);  //默认导航样式
            }
          that.current = 1;
          that.$refs.pagination.setCurrent(1);  //改变页码样式块
          //   获取指定分类下的文章列表
          that.$api.get('/portal/lists/getCategoryPostLists/category_id/'+that.idx+'?page='+that.current+'', null, res => {
            if(res.code==1){
            that.total = res.data.list.total;
            that.display = res.data.list.per_page;
            that.newspage = res.data.list.data;
          }
        })
          }
        })
        //   获取指定分类下的推荐文章
        that.$api.get('/portal/recommended/read/id/'+id+'', null, res => {
          if(res.code==1){
            that.recommend = res.data;
          }
        })
        //   获取指定分类下的热门文章
        that.$api.get('/portal/hot/read/id/'+id+'', null, res => {
          if(res.code==1){
            that.hotpage = res.data;
          }
        })
      },
      //   切换二级导航
      selectStyle (item, index,idx) {
        var that =this;
        this.$nextTick(function () {
          that.subcategory.forEach(function (item) {
            Vue.set(item,'active',false);
          });
          Vue.set(item,'active',true);

          that.idx = idx;
          that.current = 1;
          that.$refs.pagination.setCurrent(1);  //改变页码样式块
          //   获取指定分类下的文章列表
          that.$api.get('/portal/lists/getCategoryPostLists/category_id/'+that.idx+'?page='+that.current+'', null, res => {
            if(res.code==1){
              that.total = res.data.list.total;
              that.display = res.data.list.per_page;
              that.newspage = res.data.list.data;
            }
          })
        });
      },
      //   切换页码
      pagechange:function(currentPage){
        var that = this;
        that.current = currentPage;
        //   获取指定分类下的文章列表
        that.$api.get('/portal/lists/getCategoryPostLists/category_id/'+that.idx+'?page='+that.current+'', null, res => {
          if(res.code==1){
            that.newspage = res.data.list.data;
          }
        })
      }
    },
    created(){
      this.getmsg()
    }
  }
</script>
<style>
  .list{
    background-color: #EDEDED;
  }
  .main{
    padding-top: 60px;
    padding-bottom: 40px;
  }
  .banner{
    width: 100%;
  }
  .banner img{
    width: 100%;
  }
  .banner-small{
    display: none;
  }
  .menu{
    width: 100%;
    padding-left: 22px;
    padding-right: 22px;
    border-bottom: 1px solid #cccccc;
  }
  .list .menu .active{
    color: #0C347A;
    border-bottom: 4px solid #D51618;
  }
  .news{
    padding: 30px 0 20px 20px;
  }
  .list .menu div{
    font-size: 18px;
    color: #666666;
    display: inline-block;
    margin-left: 18px;
    margin-right: 18px;
    padding-bottom: 14px;
    cursor: pointer;
  }
  .list_group{
    width: 100%;
    margin-bottom: 40px;
  }
  .list_img{
    float: left;
    max-width: 234px;
    margin-right: 18px;
  }
  .list_img img{
    width: 234px;
    height: 166px;
  }
  .list_mes{
    float: left;
    width: 478px;
  }
  .mes_title{
    margin-top: 3px;
    font-size: 18px;
  }
  .mes_title a{
    color: #333333;
  }
  .mes_content1{
    padding-top: 3px;
    padding-bottom: 12px;
    font-size: 14px;
    color: #808080;
    overflow: hidden;
  }
  .mes_keyword{
    float: left;
    height: 23px;
  }
  .mes_keyword span{
    text-align: center;
    line-height: 19px;
    display: inline-block;
    color: #0B357C;
    background-color: #ffffff;
    font-size: 14px;
    padding: 0 7px;
    height: 20px;
    border: 1px solid #0B357C;
    border-radius: 5px;
    margin-right: 20px;
  }
  .mes_time{
    color: #808080;
    font-size: 14px;
    line-height: 23px;
  }
  .heigh{
    min-height: 137px;
  }
  .mes_content1{
    max-height: 105px;
  }

  /*内容右部分*/
  .article{
    background-color: #ffffff;
    max-height: 350px;
    width: 100%;
    margin-bottom: 20px;
    overflow: hidden;
  }
  .article-title{
    line-height: 40px;
    width: 100%;
    border-bottom: 1px solid #E8E8E8;
    padding-left: 10px;
    padding-right: 10px;
  }
  .article-title span:nth-child(1){
    font-size: 18px;
    color: #333333;
    font-family: '黑体';
    font-weight: 700;
  }
  .article-title span:nth-child(2){
    font-family: '黑体';
    font-size: 15px;
    float: right;
    color: #999999;
    cursor: pointer;
  }
  .article-title span:nth-child(2) i{
    display: inline-block;
    width: 15px;
    height: 15px;
    background: url('../../images/gengduo.jpg') no-repeat;
    vertical-align: middle;
    margin-left: 2px;
  }
  .article-content{
    padding-top: 15px;
    padding-bottom: 8px;
    padding: 15px 15px 8px 38px;
  }
  .article-content li{
    padding-top: 6px;
    padding-bottom: 7px;
    color: #0A367C;
    font-size: 20px;
  }
  .article-content li a{
    vertical-align: middle;
    display: inline-block;
    width: 100%;
    color: #999999;
    font-size: 16px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .yema{
    margin-top: 20px;
    margin-bottom: 20px;
  }
  /*响应式代码*/
  /*小屏情况下*/
  @media (max-width: 1200px){
    .list_mes{
      width: 340px;
    }
  }
  @media (max-width: 992px){
    .list_mes{
      width: 445px;
    }
  }
  @media (max-width: 768px) {
    .main{
      padding-top: 20px;
      padding-bottom: 20px;
    }
    .list .menu div{
      margin-left: 8px;
      margin-right: 8px;
    }
    .list_group{
      border-bottom: 1px solid #cccccc;
      padding-bottom: 5px;
    }
    .list .banner-big{
      display: none;
    }
    .list .banner-small{
      display: block;
    }
    .news{
      padding-left: 18px;
      padding-right: 18px;
    }
    .list_mes{
      width: 100%;
    }
    .list_img{
      margin-bottom: 10px;
      margin-right: 0px;
    }
    .mes_content2{
      margin-top: 5px;
    }
    .yema{
      margin-top: 0px;
    }
    .yema ul{
      float: left;
      padding-left: 0;
    }
  }
  @media (max-width: 360px){
    .list_img img{
      width: 100%;
    }
  }

  /*大屏情况下*/
  @media (min-width: 992px){
    .main-right{
      padding-left: 30px;
      padding-right: 0;
    }
  }
  @media (min-width: 768px) {
    .mes_title{
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
    .main-left{
      padding-right: 30px;
    }
    .yema ul{
      float: right;
    }
  }

</style>



