<template>
  <div class="container">
      <div class="profile">
        <p>公司简介<span>Company  profile</span><i></i></p>
        <div>
          <img src="../../images/company/jianjie.jpg" alt="">
          <!--<img :src="imgBaseUrl+info.site_thumbnail"/>-->
          <article class="article">
            <h3>{{info.site_name}}</h3>
            <span>{{info.site_seo_description}}</span>
          </article>
        </div>
      </div>
      <div class="philosophy">
        <p>公司理念<span>Company  philosophy</span><i></i></p>
        <div>
          <img src="../../images/company/linian.jpg" alt="">
        </div>
      </div>
      <div class="Memorabilia">
        <p>大事件<span>Memorabilia</span><i></i></p>
        <div>
          <img src="../../images/company/dashiji.jpg" alt="">
        </div>
      </div>
      <div class="companyStyle">
        <p>企业风采<span>Company  style</span><i></i></p>
        <div class="swiper-container">
          <div class="swiper-wrapper smallBanner">
            <div class="swiper-slide smallImg" v-for="item in img">
              <img :src="imgBaseUrl+item.image">
            </div>
          </div>
          <!-- 如果需要导航按钮 -->
          <div class="swiper-button-prev btn"></div>
          <div class="swiper-button-next btn"></div>
        </div>
      </div>
  </div>
</template>
<script>
  import {imgBaseUrl} from 'src/config/env';
  import 'swiper/dist/css/swiper.min.css';
  export default {
    data(){
      return {
        img:'',
        info:'',
        title: '关于我们 - 冀企管家',
        description: '互联网时代的企业管家',
        keywords: '冀企管家',
        imgBaseUrl
      }
    },
    metaInfo () {
      return {
        title: this.title,
        meta: [
          {                 // set meta
            name: 'description',
            content: this.description
          },
          {
            name: 'keywords',
            content:this.keywords
          }
        ]
      }
    },
    created () {
      let that=this;
      this.$api.get('/home/index/info',null, function (res) {
        if(res.code==1){
          that.info=res.data;
        }
      })
      /**
       * 企业风采接口
       */
      this.$api.get('/home/slides/12',null, function (res) {
        if(res.code===1){
          that.img=res.data;
          that.$nextTick(function () {
            var mySwiper = new Swiper('.swiper-container', {
              direction: 'horizontal',
              loop: true,
              autoplay:3000,
              initialSlide :2,
              nextButton: '.swiper-button-next',
              prevButton: '.swiper-button-prev',
              slidesPerView : '5',//'auto'
              spaceBetween: 40,
              breakpoints: {
                //当宽度小于等于320
                320: {
                  slidesPerView: 1,
                  spaceBetween: 10
                },
                //当宽度小于等于480
                480: {
                  slidesPerView: 2,
                  spaceBetween: 20
                },
                //当宽度小于等于640
                640: {
                  slidesPerView: 3,
                  spaceBetween: 30
                }
              },
            })
          })
        }
      })
      /**
       * 标题
       */
      this.$api.get('/home/index/seo/id/1',null, function (res) {
        if(res.code==1){
          that.title = res.data.seo_title;
          that.description = res.data.seo_describe;
          that.keywords = res.data.seo_keywords;
        }
      })
    }
  }
</script>

<style scoped>
  li{
    list-style: none;
  }
  @media screen and (min-width: 478px){
    /**
      公司简介
     */
    .profile{
      height: 534px;
    }
    .profile>div{
      width: 100%;
      display: flex;
      margin-top: 60px;
    }
    .profile>div>img{
      display: block;
      height: 268px;
      width: 462px;

    }
    .article{
      margin-left: 20px;
    }
    .article>h3{
      font-size: 22px;
      color: #666666;
      height: 50px;
      line-height: 50px;
    }
    .article>span{
      line-height: 25px;
      font-size: 16px;
      color: #666666;
    }
    /**
      公司理念
    */
    .philosophy>div{
      margin-top: 60px;
    }
    .philosophy>div>img{
      width: 100%;
    }

    .Memorabilia>div{
      margin-top: 60px;
    }
    .Memorabilia>div>img{
      width: 80%;
      display: block;
      margin: 0 auto;
    }

    .swiper-container{
      width: 100%;
      padding:0  40px;
    }

  }
  @media screen and (max-width: 479px) {
    /**
      公司简介
     */
    .profile>div{
      width: 100%;
      display: block;
      margin-top: 34px;
    }
    .profile>div>img{
      display: block;
      width: 80%;
      margin: 0 auto;
    }
    .article{
      width: 100%;
      display: block;
    }
    .article>h3{
      width: 100%;
      display: block;
      text-align: center;
      font-size: 26px;
    }
    .article>span{
      line-height: 28px;
      font-size: 16px;
      color: #666666;
    }
    /**
      公司理念
     */
    .philosophy>div{
      margin-top: 34px;
    }
    .philosophy>div>img{
      width: 100%;
    }

    .Memorabilia>div{
      margin-top: 60px;
    }
    .Memorabilia>div>img{
      width: 80%;
      display: block;
      margin: 0 auto;
    }

  }




  /**
    公司简介
   */
  .profile>p{
    text-align: center;
    font-size: 30px;
    color:#0c347c;
    padding-top: 50px;
  }
  .profile>p>span{
    display: block;
    margin-top: 12px;
    font-size: 12px;
    text-align: center;
  }
  .profile>p>i{
    display: block;
    width: 30px;
    height: 2px;
    background: #e60508;
    margin: 0 auto;
    margin-top: 12px;
    text-align: center;
  }
  /**
    公司理念
   */
  .philosophy>p{
    text-align: center;
    font-size: 30px;
    color:#0c347c;
    padding-top: 50px;
  }
  .philosophy>p>span{
    display: block;
    margin-top: 12px;
    font-size: 12px;
    text-align: center;
  }
  .philosophy>p>i{
    display: block;
    width: 30px;
    height: 2px;
    background: #e60508;
    margin: 0 auto;
    margin-top: 12px;
    text-align: center;
  }


  .Memorabilia>p{
    text-align: center;
    font-size: 30px;
    color:#0c347c;
    padding-top: 50px;
  }
  .Memorabilia>p>span{
    display: block;
    margin-top: 12px;
    font-size: 12px;
    text-align: center;
  }
  .Memorabilia>p>i{
    display: block;
    width: 30px;
    height: 2px;
    background: #e60508;
    margin: 0 auto;
    margin-top: 12px;
    text-align: center;
  }

  /**
      banner图
   */
  .swiper-container{
    width: 100%;
    /*padding:0  40px;*/
  }
  .companyStyle>p{
    text-align: center;
    font-size: 30px;
    color:#0c347c;
    padding-top: 50px;
  }
  .companyStyle>p>span{
    display: block;
    margin-top: 12px;
    font-size: 12px;
    text-align: center;
  }
  .companyStyle>p>i{
    display: block;
    width: 30px;
    height: 2px;
    margin-top: 12px;
    text-align: center;
  }
  .smallImg{
    margin-top: 60px;
    margin-bottom: 90px;
    width: 174px;
    height: 120px;
  }
  .smallImg>img{
    width: 174px;
    height: 120px;
    display: block;
    margin: 0 auto;
  }
</style>



