<template>
    <div class="apply">
      <div class="container">
        <div class="sq_title">企业申请</div>
        <div class="sq_box">
          <form id="sq" @submit.prevent="submit">
            <div class="form-group">
              <label class="control-label" for="cname"><i>*</i>企业名称</label>
              <input id="cname" style="width: 60%" type="text" class="form-control" v-model='datamsg.name' placeholder="请输入公司名称">
            </div>
            <div class="form-group">
              <label class="control-label" for="xingzhi"><i>*</i>企业性质</label>
              <select id="xingzhi" style="width: 60%" placeholder="公司类型" v-model='datamsg.xingzhi' class="form-control">
                <option disabled value="">请选择</option>
                <option value="有限公司">有限公司</option>
                <option value="个体工商">个体工商</option>
              </select>
            </div>
            <div class="form-group">
              <label class="control-label"><i>*</i>所在地区</label>
              <city-select v-model="cityInfo"></city-select>
            </div>
            <div class="form-group">
              <label class="control-label" for="address"><i>*</i>详细地址</label>
              <input id="address" style="width: 60%" type="text" class="form-control" v-model="datamsg.address" placeholder="请输入公司地址">
            </div>
            <div class="form-group">
              <label class="control-label" for="people"><i>*</i>联系人</label>
              <input id="people" style="width: 60%" type="text" class="form-control" v-model="datamsg.people" placeholder="请输入联系人">
            </div>
            <div class="form-group">
              <label class="control-label" for="contact"><i>*</i>联系方式</label>
              <input id="contact" style="width: 60%" type="text" class="form-control" v-model="datamsg.contact" placeholder="请输入联系人岗位">
            </div>
            <div class="form-group">
              <label class="control-label" for="gangwei"><i>*</i>联系人岗位</label>
              <input id="gangwei" style="width: 60%" type="text" class="form-control" v-model="datamsg.gangwei" placeholder="请输入联系人岗位">
            </div>
            <div class="form-group yzm">
              <label class="control-label" for="captch"><i>*</i>验证码</label>
              <input id="captch" type="text" class="form-control" v-model="datamsg.code" placeholder="请输入验证码">
              <img :src="baseimg" alt="" @click='tabimg'/>
            </div>
            <div class="form-group text-center">
              <button class="sq-btn" type="button" @click='btn'>提交</button>
            </div>
          </form>
        </div>
      </div>
    </div>
</template>
<script>
    import CitySelect from 'src/components/CitySelect/CitySelect.vue'  //省市区插件
    export default {
      data() {
      return {
        cityInfo: '',
        baseimg:'',
        title: '企业申请 - 冀企管家',
        description: '互联网时代的企业管家',
        keywords: '冀企管家',
        datamsg:{
          name:'',
          xingzhi:'',
          seachprov:'',
          homecity:'',
          seachdistrict:'',
          address:'',
          people:'',
          contact:'',
          gangwei:'',
          code:''
        }
      }
    },
      metaInfo () {
        return {
          title: this.title,
          meta: [
            {                 // set meta
              name: 'description',
              content: this.description
            },
            {
              name: 'keywords',
              content:this.keywords
            }
          ]
        }
      },
    components: {
      CitySelect
    },
    methods:{
      btn:function(){
        var that = this;
        this.datamsg.seachprov = this.cityInfo.province.name;
        this.datamsg.homecity = this.cityInfo.city.name;
        this.datamsg.seachdistrict = this.cityInfo.block.name;
        if ($.trim(that.datamsg.name)=="") {
          alert("请输入公司名称");
          return false;
        }
        const formData = new FormData()
        formData.append('name', that.datamsg.name)
        formData.append('xingzhi', that.datamsg.xingzhi)
        formData.append('seachprov', that.datamsg.seachprov)
        formData.append('homecity', that.datamsg.homecity)
        formData.append('seachdistrict', that.datamsg.seachdistrict)
        formData.append('address', that.datamsg.address)
        formData.append('people', that.datamsg.people)
        formData.append('contact', that.datamsg.contact)
        formData.append('gangwei', that.datamsg.gangwei)
        formData.append('code', that.datamsg.code)
        formData.append('code_id', that.datamsg.code_id)
        //   获取提交问题
        that.$api.post('/home/index/apply', formData , res => {
        if(res.code==0){
          alert(res.msg)
        }
          if(res.code==1){
            this.$router.push({path:'/success'})
          }
        }, rrr  =>{
        })
      },
      //  获取验证码
      getcaptcha:function(){
        var that = this;
        that.$api.get('/home/index/captcha', null, res => {
          if(res.code==1){
          that.baseimg = res.data;
        }
      })
      },
      tabimg:function(){
        this.getcaptcha()
      }
    },
    created(){
        var that = this;
      this.getcaptcha();
//      获取seo
      this.$api.get('/home/index/seo/id/3',null, function (res) {
        if(res.code==1){
          that.title = res.data.seo_title;
          that.description = res.data.seo_describe;
          that.keywords = res.data.seo_keywords;
        }
      })
    }
    }
</script>
<style>
  /*表单样式*/
  .apply{
    background-color: #EEEEEE;
  }
  .sq_title{
    font-size: 24px;
    color: #000;
    font-family: "黑体";
    text-align: center;
    border-bottom: 1px solid #CCCCCC;
    line-height: 80px;
  }
  .sq_box{
    padding-top: 50px;
  }
  .form-group{
    overflow: hidden;
    margin-bottom: 40px;
  }
  .control-label{
    float: left;
    width: 150px;
    line-height: 40px;
    margin-right: 40px;
    font-size: 24px;
    font-weight: 400;
  }
  .control-label i{
    color: red;
    display: inline-block;
    width: 20px;
    line-height: 40px;
    text-align: left;
  }
  .form-control{
    height: 40px;
    float: left;
    width: 70%;
    font-size: 18px;
  }
  .form-group .form-control option{
    height: 37px;
  }
  .sq-btn{
    width: 300px;
    height: 50px;
    background-color: #0B357C;
    color: #ffffff;
    border-radius: 8px;
    font-size: 26px;
    margin-top: 20px;
    border: none;
    margin-bottom: 40px;
  }
  .yzm img{
    width: 200px;
    height: 50px;
  }
  .yzm input{
    width: 200px;
    height: 50px;
    margin-right: 20px;
  }
  /*小屏情况下*/
  @media (max-width: 768px) {
    .sq-btn{
      width: 200px;
      height: 50px;
      border-radius: 5px;
      font-size: 22px;
    }
  }
</style>



